import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import{ DataService } from '../services/data.service';

@Component({
  selector: 'app-alarm',
  templateUrl: './alarm.component.html',
  styleUrls: ['./alarm.component.scss']
})
export class AlarmComponent implements OnInit {

  alarms = [];
  pageList = [];
  pages = 1;
  pageSize = 10;
  newPages = 10;
  selPage = 1;
  isEditing = false;
  isLoading = true;
  items = [];
  isCurrentPage = false;

  constructor(private http:Http,
              private dataService:DataService) {
  }

  ngOnInit() {
    this.getAlarms();
  }

  getAlarms() {
    this.dataService.getAlarms().subscribe(
        data => {
        this.alarms = data;
        this.pages = Math.ceil(this.alarms.length / this.pageSize);
        this.newPages = this.pages > 10 ? 10 : this.pages;
        this.items = this.alarms.slice(0, this.pageSize);
        for (var i = 0; i < this.newPages; i++) {
          this.pageList.push(i + 1);
        }
      },
        error => console.log(error),
      () => this.isLoading = false
    );
  }

  setData() {
    this.items = this.alarms.slice((this.pageSize * (this.selPage - 1)), this.selPage * this.pageSize);
  }

  selectPage = function (page:any) {
    if (page < 1 || page > this.pages) {
      return;
    }
    if (page > 2) {
      var newPageList = [];
      for (var i = (page - 3); i < ((page + 7) > this.pages ? this.pages : (page + 7)); i++) {
        newPageList.push(i + 1);
      }
      this.pageList = newPageList;
    }
    this.selPage = page;
    this.setData();
    this.isActivePage(page);
    console.log("选择的页 " + page);
  }

  isActivePage = function (page:any) {
    this.selPage == page;
  }

  previousPage = function () {
    this.selectPage(this.selPage - 1);
  }

  nextPage = function () {
    this.selectPage(this.selPage + 1);
  }

}



